import {fetchIcons} from "@plover/api";

/*
Example data structure

const state = {
    libraries: {
        lib_slug: {
            icon_slug: {s:'svg raw string', t:['tags']}
        }
    }
}
 */
const DEFAULT_STATE = {
    libraries: {},
};

const actions = {
    fetchIconsFromAPI(library) {
        return {
            type: 'FETCH_ICONS_FROM_API',
            library,
        };
    },
    setIcons: (library, {icons}) => {
        return {
            type: 'SET_LIBRARY_ICONS',
            library,
            icons,
        };
    }
};

const iconsStore = {
    name: 'plover/icons',
    options: {
        reducer(state = DEFAULT_STATE, action) {
            switch (action.type) {
                case 'SET_LIBRARY_ICONS': {
                    state = {
                        ...state,
                        libraries: {
                            ...state.libraries,
                            [action.library]: action.icons,
                        }
                    };
                }
            }

            return state;
        },
        actions,
        selectors: {
            getIcons: (state, library) => {
                const icons = state.libraries[library] ?? {}
                return {library, icons};
            }
        },
        controls: {
            FETCH_ICONS_FROM_API({library}) {
                return fetchIcons(library);
            },
        },
        resolvers: {
            * getIcons(library) {
                const data = yield actions.fetchIconsFromAPI(library);
                return actions.setIcons(library, data);
            }
        }
    }
};

export default iconsStore;
