/**
 * WordPress dependencies
 */
import { InspectorControls } from '@wordpress/block-editor';
import {
  PanelBody,
  SelectControl,
  TextControl,
  BaseControl,
  Button,
  Spinner,
  __experimentalToggleGroupControl as ToggleGroupControl,
  __experimentalToggleGroupControlOption as ToggleGroupControlOption,
  __experimentalDropdownContentWrapper as DropdownContentWrapper,
  __experimentalVStack as VStack
} from '@wordpress/components';
import { __, sprintf } from '@wordpress/i18n';
import { useEffect, useState } from '@wordpress/element';
import { resolveSelect } from '@wordpress/data';

/**
 * Plover dependencies
 */
import { Popover, UnitSlider, FixedSizeGrid as Grid, AutoSizer, Tips } from '@plover/components';
import { getExtensionSetting, titleCase } from '@plover/utils';

/**
 * External dependencies
 */
import clsx from 'clsx';

/**
 * Internal dependencies
 */
import { isSlugEqual } from './utils';

const ICONS_PRE_ROW = 5;

function IconPicker ({ current, icons, onChange }) {

  const [keyword, setKeyword] = useState('');

  const filteredIcons = icons.filter(({ tags, slug, name }) => {
    if (keyword.trim() === '') {
      return true;
    }

    const keys = (tags ?? []).join('') + slug + name;
    return keys.toLowerCase().includes(keyword.trim().toLowerCase());
  });

  return (
    <DropdownContentWrapper
      paddingSize="none"
      className="plover-icon-settings-panel__popover-content"
    >
      <VStack spacing="12px">
        <TextControl
          label={__('Filter', 'plover')}
          value={keyword}
          onChange={setKeyword}
        />

        <div className="plover-icon-settings-panel__icon-list" style={{ width: '300px', height: '240px' }}>
          <AutoSizer>
            {({ width, height }) => {
              // Pay attention to the width of sidebar
              const ICONS_PREVIEW_SIZE = Math.floor((width - 16) / ICONS_PRE_ROW);
              const COLUMN_WIDTH = ICONS_PREVIEW_SIZE;
              const ROW_HEIGHT = ICONS_PREVIEW_SIZE;

              return <Grid
                itemData={filteredIcons}
                columnCount={ICONS_PRE_ROW}
                columnWidth={COLUMN_WIDTH}
                rowCount={Math.ceil(filteredIcons.length / ICONS_PRE_ROW)}
                rowHeight={ROW_HEIGHT}
                height={height}
                width={width}
              >
                {({ columnIndex, rowIndex, style }) => {
                  const i = ICONS_PRE_ROW * rowIndex + columnIndex;
                  const icon = filteredIcons[i];
                  if (!icon) {
                    return null;
                  }

                  return (
                    <div style={style} className="plover-icon-settings-panel__icon-preview-wrap">
                      <button
                        aria-label={icon.name}
                        className={clsx({
                          'plover-icon-settings-panel__icon-preview': true,
                          'active': isSlugEqual(current, icon.slug),
                        })}
                        dangerouslySetInnerHTML={{ __html: icon.svg }}
                        onClick={() => {
                          onChange({
                            icon: String(icon.slug),
                            svg: icon.svg
                          });
                        }}
                      />
                    </div>
                  );
                }}
              </Grid>;
            }}
          </AutoSizer>
        </div>

        {filteredIcons.length <= 0 && (
          <p className="plover-icon-settings-panel__no-result">
            {__('No icons', 'plover')}
          </p>
        )}
      </VStack>
    </DropdownContentWrapper>
  );
}

function IconSelector ({ attributes, setAttributes }) {
  const { iconSlug, iconSvgString, iconLibrary } = attributes;

  const [loading, setLoading] = useState(false);
  const [icons, setIcons] = useState([]);

  const libraries = getExtensionSetting('icon', 'libraries', {});

  const currentLibrary = libraries.find(({ slug }) => isSlugEqual(slug, iconLibrary)) ?? {};
  const currentIcon = icons.find(({ slug }) => isSlugEqual(slug, iconSlug));

  useEffect(() => {
    if (!currentLibrary?.slug || currentLibrary?.slug === 'none') {
      setIcons([]);
      return;
    }

    setLoading(true);

    resolveSelect('plover/icons').getIcons(currentLibrary?.slug)
      .then(({ library, icons }) => {
        if (isSlugEqual(library, iconLibrary)) {
          setIcons(icons ?? []);
        }
      })
      .finally(() => {
        setLoading(false);
      });

  }, [iconLibrary]);

  return (
    <>
      <SelectControl
        label={__('Icon library', 'plover')}
        value={currentLibrary?.slug ?? 'none'}
        options={[
          { label: 'None', value: 'none' },
          ...libraries.map(({ name, slug }) => {
            return { label: name, value: slug };
          })
        ]}
        onChange={(value) => {
          setAttributes({
            iconLibrary: value === 'none' ? null : value,
            iconSlug: null,
            iconSvgString: null,
          });
        }}
      />
      <BaseControl label={__('Select icon', 'plover')}>
        {loading ? (<div><Spinner/></div>) : (
          <Popover
            className="plover-icon-settings-panel__icon-picker"
            toggle={{
              icon: currentLibrary?.slug ? iconSvgString : '',
              label:
                currentLibrary?.name ?
                  currentLibrary?.name + ' / ' + (currentIcon ? titleCase(currentIcon?.name) : __('None', 'plover'))
                  : __('None', 'plover')
            }}
            renderContent={() => (
              <IconPicker
                current={iconSlug}
                icons={icons}
                onChange={({ icon, svg }) => {
                  setAttributes({
                    iconSlug: icon,
                    iconSvgString: svg
                  });
                }}
              />
            )}
          />
        )}
      </BaseControl>
    </>
  );
}

export default function IconControls ({ attributes, setAttributes, position = true, clear = true }) {
  return (
    <InspectorControls>
      <PanelBody
        title={__('Icon', 'plover')}
        className="block-editor-plover-inspector__icons"
      >
        <IconSelector
          attributes={attributes}
          setAttributes={setAttributes}
        />

        <Tips>
          <p>
            <span dangerouslySetInnerHTML={{
              __html: sprintf(
                __('Use the %s plugin to get more icons from the Icon Library.', 'plover'),
                '<a href="https://wpplover.com/plugins/plover-kit/" target="_blank">' + __('Plover Kit', 'plover') + '</a>'
              )
            }}/>
            <a href="https://wpplover.com/docs/plover-kit/modules/icon-library/" target="_blank">
              {__('Documentation â†—', 'plover')}
            </a>
          </p>
        </Tips>

        {position && (
          <ToggleGroupControl
            isBlock
            label={__('Icon position', 'plover')}
            value={attributes.iconPosition}
            onChange={(position) => {
              setAttributes({
                iconPosition: position,
              });
            }}
          >
            <ToggleGroupControlOption value="left" label={__('Left', 'plover')}/>
            <ToggleGroupControlOption value="right" label={__('Right', 'plover')}/>
          </ToggleGroupControl>
        )}

        <UnitSlider
          label={__('Icon Size', 'plover')}
          value={attributes.iconSize}
          min={10}
          onChange={(value) => {
            setAttributes({
              iconSize: value,
            });
          }}
        />

        {clear && (
          <Button variant="tertiary" onClick={() => {
            setAttributes({
              iconLibrary: null,
              iconSlug: null,
              iconSvgString: null,
            });
          }}>
            {__('Clear icon', 'plover')}
          </Button>
        )}
      </PanelBody>
    </InspectorControls>
  );
}
