/**
 * WordPress dependencies
 */
import {
  AlignmentControl,
  BlockControls,
  useBlockProps,
  useBlockEditingMode,
} from '@wordpress/block-editor';
import { ResizableBox } from '@wordpress/components';
/**
 * External dependencies
 */
import clsx from 'clsx';

/**
 * Internal dependencies
 */
import IconControls from './IconControls';

export default function IconVariationEdit (props) {
  const {
    attributes,
    setAttributes,
    className,
    isSelected,
    isRTL,
  } = props;

  const {
    align,
    iconSize,
    iconSvgString,
  } = attributes;

  const blockEditingMode = useBlockEditingMode();

  const classes = clsx(className, {
    [`has-text-align-${align}`]: align,
  });
  const blockProps = useBlockProps({ className: classes });

  const controls = (
    <>
      {blockEditingMode === 'default' && (
        <BlockControls group="block">
          <AlignmentControl
            value={align}
            onChange={(newAlign) =>
              setAttributes({
                align: newAlign,
                dropCap: false
              })
            }
          />
        </BlockControls>
      )}

      <IconControls position={false} clear={false} {...props} setAttributes={(attrs) => {
        setAttributes(attrs?.iconSvgString === undefined ? attrs : {
          ...attrs,
          content: '',
        });
      }}/>
    </>
  );

  let showRightHandle = false;
  let showLeftHandle = false;

  if (align === 'center') {
    // When the image is centered, show both handles.
    showRightHandle = true;
    showLeftHandle = true;
  } else if (isRTL) {
    // In RTL mode the image is on the right by default.
    // Show the right handle and hide the left handle only when it is aligned left.
    // Otherwise, always show the left handle.
    showRightHandle = align === 'left';
    showLeftHandle = align !== 'left';
  } else {
    // Show the left handle and hide the right handle only when the image is aligned right.
    // Otherwise always show the right handle.
    showLeftHandle = align === 'right';
    showRightHandle = align !== 'right';
  }

  return (
    <>
      {controls}
      <div {...blockProps}>
        <ResizableBox
          enable={{
            bottom: true,
            left: showLeftHandle,
            right: showRightHandle,
            top: false,
          }}
          lockAspectRatio
          showHandle={isSelected}
          style={{
            display: 'inline-block'
          }}
          size={{
            width: iconSize,
            height: iconSize,
          }}
          onResizeStop={(_event, _direction, _elt, delta) => {
            const newValue = parseInt(parseInt(iconSize) + delta.width, 10) + 'px';
            setAttributes({
              iconSize: newValue,
            });
          }}
        >
          <span
            dangerouslySetInnerHTML={{
              __html: iconSvgString
            }}
          />
        </ResizableBox>
      </div>
    </>
  );
}