/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks';
import { createHigherOrderComponent } from '@wordpress/compose';
import { register, createReduxStore, registerStore } from '@wordpress/data';
import { getBlockVariations, registerBlockVariation } from '@wordpress/blocks';
import domReady from '@wordpress/dom-ready';
import { __ } from '@wordpress/i18n';

/**
 * External dependencies
 */
import clsx from 'clsx';

/**
 * Plover dependencies
 */
import { getExtensionSetting } from '@plover/utils';
import { starOutline } from '@plover/icons';

/**
 * Internal dependencies
 */
import IconControls from './IconControls';
import IconVariationEdit from './IconVariationEdit';
import iconsStore from './store';
import { isSlugEqual } from './utils';

import './style.scss';

const defaultIcon = {
  'library': 'plover-core',
  'slug': 'star',
  'size': '24px',
  'svg': '<svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" aria-hidden="true" data-slot="icon"><path stroke-linecap="round" stroke-linejoin="round" d="M11.48 3.499a.562.562 0 0 1 1.04 0l2.125 5.111a.563.563 0 0 0 .475.345l5.518.442c.499.04.701.663.321.988l-4.204 3.602a.563.563 0 0 0-.182.557l1.285 5.385a.562.562 0 0 1-.84.61l-4.725-2.885a.562.562 0 0 0-.586 0L6.982 20.54a.562.562 0 0 1-.84-.61l1.285-5.386a.562.562 0 0 0-.182-.557l-4.204-3.602a.562.562 0 0 1 .321-.988l5.518-.442a.563.563 0 0 0 .475-.345L11.48 3.5Z"></path></svg>'
};

const defaultAttributes = {
  iconLibrary: {
    type: 'string',
  },
  iconSlug: {
    type: 'string',
  },
  iconPosition: {
    type: 'string',
    default: 'right',
  },
  iconSize: {
    type: 'string',
    default: '18px'
  },
  iconSvgString: {
    type: 'string'
  }
};

addFilter(
  'blocks.registerBlockType',
  'plover/icon-attributes',
  (settings, name) => {
    const supported_blocks = getExtensionSetting('icon', 'blocks', []);
    if (supported_blocks.includes(name)) {
      settings.attributes = {
        ...settings.attributes,
        ...getExtensionSetting('icon', 'attributes', defaultAttributes)
      };
    }

    return settings;
  }
);

addFilter(
  'editor.BlockEdit',
  'plover/icon-controls',
  (BlockEdit) => {
    return (props) => {
      const { name, attributes } = props;
      const supported_blocks = getExtensionSetting('icon', 'blocks', []);

      if (!supported_blocks.includes(name)) {
        return <BlockEdit {...props}/>;
      }

      if (name === 'core/paragraph') {
        // normal paragraph
        if (!attributes?.className?.includes('is-style-icon')) {
          return <BlockEdit {...props} />;
        }

        // plover icon variation
        return <IconVariationEdit {...props}/>;
      }

      return (
        <>
          <BlockEdit {...props}/>
          <IconControls {...props}/>
        </>
      );
    };
  },
  5
);

addFilter(
  'editor.BlockListBlock',
  'plover/with-icon-styles',
  createHigherOrderComponent((BlockListBlock) => {
    return (props) => {
      const supported_blocks = getExtensionSetting('icon', 'blocks', []);
      const { attributes, name, clientId } = props;
      const icon_libraries = getExtensionSetting('icon', 'libraries', {});

      if (!supported_blocks.includes(name)) {
        return <BlockListBlock {...props}/>;
      }

      const classes = clsx(props?.className, {
        ['icon-library-not-available']: !icon_libraries.find(({ slug }) => isSlugEqual(slug, attributes?.iconLibrary)),
        [`has-icon__${attributes?.iconSlug}`]: attributes?.iconSlug,
        [`has-icon-position__${attributes?.iconPosition}`]: attributes?.iconPosition,
      });

      let css = '';

      if (attributes?.iconSvgString) {
        css += `--wp--custom--icon--url: url('data:image/svg+xml;utf8,${attributes.iconSvgString}');`;

        if (attributes?.iconSize) {
          css += `--wp--custom--icon--size: ${attributes.iconSize};`;
        }
      }

      return (
        <>
          {css && <style>{'#block-' + clientId + '{' + css + '}'}</style>}
          <BlockListBlock {...props} className={classes}/>
        </>
      );
    };
  }, 'withIcon')
);

domReady(() => {
  const supported_blocks = getExtensionSetting('icon', 'blocks', []);
  if (!supported_blocks.includes('core/paragraph')) {
    return;
  }

  const variations = getBlockVariations('core/paragraph');
  if (!variations.some(variation => 'plover-icon' === variation.name)) {
    registerBlockVariation('core/paragraph', {
      name: 'plover-icon',
      title: __('Plover: Icon', 'plover'),
      icon: starOutline,
      description: __('Insert a SVG icon.', 'plover'),
      category: 'media',
      keywords: ['icon', 'plover', 'svg'],
      attributes: {
        className: 'is-style-icon',
        align: 'center',
        iconLibrary: defaultIcon.library,
        iconSlug: defaultIcon.slug,
        iconSize: defaultIcon.size,
        iconSvgString: defaultIcon.svg,
        content: '',
      },
      scope: ['block', 'inserter', 'transform'],
      isActive: (blockAttributes) => {
        return blockAttributes && blockAttributes?.className?.includes('is-style-icon');
      },
    });
  }
});

if (register) {
  register(createReduxStore(iconsStore.name, iconsStore.options));
} else {
  // WP 5.4 - WP5.6
  registerStore(iconsStore.name, iconsStore.options);
}
